.card{
    position: relative;
    top: 18vh;
    background: #81c5be;
    font-size: 2em;
    color: white;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    height: 320px;
    width: 100%;
    border: 0.1px solid #81c5be;
    border-radius: 12px;
    margin: 5px;
    box-shadow: 0px 0px 4px 1px;
    background: #482ff7;
    top: 2vh;
}
.Cknow{
    border: 1px solid #482ff7;
    background: #482ff7;
    background: #ffffff;
    box-shadow: 0px 0px 0px 0px;
    border-radius: 6px;
    padding: 5px 30px;
    cursor: pointer;
    color: #482ff7;

}
.k{
    position: relative;
    clip-path: polygon(0 0,100% 0,100% 85%,0 100%);
    background: violet;
}


.grid{
    position: relative;
    top: -5vh;
    display: grid;
    margin: 10px;
    grid-template-columns: repeat(auto-fit,minmax(300px, 1fr)) ;
    grid-template-rows: 1fr 1fr 1fr;
    place-items: center center;
    grid-gap: 10px;
    padding: 20px;
    overflow: hidden;
    height: 90vh;
}
.showc{
    text-align: center;
    width: 100%;
    margin: auto;
    padding: 20px 50px;
    background: #5a5a5a36;
    font-size: 2em;
    cursor: pointer;
    position: relative;
    bottom: 0vh;
    height: 11vh;
    border-radius: 4% 0%;
}
@media only screen and (max-width: 768px){
.grid{
    top: 7vh;
    margin: 0px;
    padding: 0px;
    margin-bottom: 15px;
}
.card{
    height: 240px;
    width: 95%;
}
}